const colors = {
  returned: 'red',
  canceled: 'red',
  completed: 'green',
  pending: 'teal',
  return: 'grey',
  'not as described': 'grey',
  'invited': 'blue',
  'expired': 'red',
  'accepted': 'green',
  unlinked: 'grey',
  linked: 'green',
  'pending request': 'orange'
}
const sellingTypeColors = {
  'easy offer': 'blue',
  'instant offer': 'green',
  'custom offer': 'orange'
}
const color = name => {
  if(typeof name === 'string') {
    if(colors.hasOwnProperty(name.toLowerCase())) return colors[name.toLowerCase()]
  }
  return 'teal'
}

const sellingTypeColor = name => {
  if(typeof name === 'string') {
    if(sellingTypeColors.hasOwnProperty(name.toLowerCase())) return sellingTypeColors[name.toLowerCase()]
  }
  return 'teal'
}

export {
  color,
  sellingTypeColor
}